import React, { useState } from "react";
import CreateIcon from "@material-ui/icons/Create";
import {
    Box, Button, FormControl, InputLabel, MenuItem, Select, Snackbar, Table,
    TableBody, TableCell, TableHead, TableRow, TextField
} from "@material-ui/core";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import AddBoxIcon from "@material-ui/icons/AddBox";
import DoneIcon from "@material-ui/icons/Done";
import ClearIcon from "@material-ui/icons/Clear";
import { makeStyles } from "@material-ui/core/styles";
import Alert from "@material-ui/lab/Alert";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";

// Creating styles
const useStyles = makeStyles({
    root: {
        "& > *": {
            borderBottom: "unset",
        },
    },
    table: {
        minWidth: 650,
    },
    snackbar: {
        bottom: "104px",
    },
});

function DividendCalculator() {
    // Creating style object
    const classes = useStyles();

    // Defining a state named rows
    // which we can update by calling on setRows function
    const [rows, setRows] = useState([
        {
            id: 1, stock: "",
            current: {
                price: "", frequency: "3", shares: "", yield: ""
            },
            projected: {
                price: "", dividend: "", shares: "", value: ""
            }
        }
    ]);

    // Initial states
    const [totalDividend, setTotalDividend] = React.useState(25);
    const [totalValue, setTotalValue] = React.useState(25);
    const [years, setYears] = React.useState(25);
    const [initialInvestment, setInitialInvestment] = React.useState(50000);
    const [stockGrowth, setStockGrowth] = React.useState(0);
    const [dividendGrowth, setDividendGrowth] = React.useState(0);
    const [open, setOpen] = React.useState(false);
    const [isEdit, setEdit] = React.useState(false);
    const [disable, setDisable] = React.useState(true);
    const [showConfirm, setShowConfirm] = React.useState(false);
    const [showMessage, setShowMessage] = React.useState(false);

    // Function For closing the alert snackbar
    const handleClose = (event, reason) => {
        if (reason === "clickaway") {
            return;
        }
        setOpen(false);
    };

    // Function For adding new row object
    const handleAdd = () => {
        setRows([
            ...rows,
            {
                id: rows.length + 1, stock: "",
                current: {
                    price: "", frequency: "3", shares: "", yield: ""
                },
                projected: {
                    price: "", dividend: "", shares: "", value: "", yield: "",
                    iPrice: "", iDividend: "", iShares: "", iValue: ""
                }
            },
        ]);
        setEdit(true);
    };

    // Function to handle edit
    const handleEdit = (i) => {
        // If edit mode is true setEdit will
        // set it to false and vice versa
        setEdit(!isEdit);
    };

    // Function to handle save
    const handleSave = () => {
        setEdit(!isEdit);
        setRows(rows);
        console.log("saved : ", rows);
        setDisable(true);
        setOpen(true);
    };

    // The handleInputChange handler can be set up to handle
    // many different inputs in the form, listen for changes
    // to input elements and record their values in state
    const handleInputChange = (e, index) => {
        setDisable(false);
        const { name, value } = e.target;
        const list = [...rows];
        if (name === "stock") {
            list[index][name] = value;
        } else {
            list[index]["current"][name] = value;
        }
        setRows(list);
    };

    // Showing delete confirmation to users
    const handleConfirm = () => {
        setShowConfirm(true);
    };

    // Handle the case of delete confirmation where
    // user click yes delete a specific row of id:i
    const handleRemoveClick = (i) => {
        const list = [...rows];
        list.splice(i, 1);
        setRows(list);
        setShowConfirm(false);
    };

    // Handle the case of delete confirmation
    // where user click no
    const handleNo = () => {
        setShowConfirm(false);
    };

    const handleCalculate = () => {
        const lyears = Number(years);
        const lstockGrowth = Number(stockGrowth);
        const ldividendGrowth = Number(dividendGrowth);
        const distribution = 100 / rows.length;
        const distributionAmount = Number(initialInvestment) * (Number(distribution) / 100).toFixed(2)
        let totalProjectedValue = 0;
        let totalProjectedDividend = 0;
        for (let j = 0; j < lyears; j++) {
            totalProjectedValue = 0;
            totalProjectedDividend = 0;
            rows.map((row, i) => {
                const currentShares = Number(Number(distributionAmount) / Number(row.current.price)).toFixed(2)
                row.current.distribution = Number(distribution).toFixed(2) + "%";
                row.current.shares = currentShares
                let dYield = 0;
                let pPrice = 0;
                let pShares = 0;
                let pValue = 0;
                if (j === 0) {
                    dYield = Number(row.current.yield).toFixed(2);
                    pPrice = Number(row.current.price).toFixed(2);
                    pShares = Number(row.current.shares).toFixed(2);
                } else {
                    dYield = Number(row.projected.yield).toFixed(2);
                    pPrice = Number(row.projected.price).toFixed(2);
                    pShares = Number(row.projected.shares).toFixed(2);
                }
                pValue = Number(pPrice) * Number(pShares);
                row.projected.value = pValue;
                row.projected.dividend = Number(pValue) * Number(dYield) / 100
                row.projected.price = Number(( Number(pPrice) * Number(lstockGrowth)  / 100 ) + Number(pPrice)).toFixed(2)
                const newShares = Number(Number(row.projected.dividend) / Number(row.projected.price)).toFixed(2)
                row.projected.shares = Number(Number(newShares) + Number(pShares)).toFixed(2)
                row.projected.yield = (Number(dYield) * Number(dividendGrowth) / 100) + Number(dYield);
                // console.log(dYield + " :::: " + Number(row.projected.dividend) + " :::: " + Number(row.projected.price) + " :::: " + newShares + " ::: " + row.projected.shares)
                totalProjectedValue = Number(totalProjectedValue) + Number(row.projected.value)
                totalProjectedDividend = Number(totalProjectedDividend) + Number(row.projected.dividend)
            })
        }
        setTotalDividend(totalProjectedDividend)
        setTotalValue(totalProjectedValue)
        setShowMessage(true);
    }

    return (
        <div>
            {/*<TableBody>*/}
                <Box margin={1}>
                    <div style={{ display: "flex", justifyContent: "space-between" }}>
                        <div>
                            <div>
                                <Button onClick={handleAdd}>
                                    <AddBoxIcon onClick={handleAdd} />
                                    Add new row
                                </Button>
                            </div>
                        </div>
                    </div>
                    <TableRow align="center"> </TableRow>

                    <Table
                        className={classes.table}
                        size="small"
                        aria-label="a dense table"
                        style={{maxWidth: "75%", marginLeft: "3%"}}
                    >
                        <TableHead style={{backgroundColor: "lightgray"}}>
                            <TableRow>
                                <TableCell align="center">Stock</TableCell>
                                <TableCell align="center">Price</TableCell>
                                <TableCell align="center">Dividend Yield</TableCell>
                                <TableCell align="center">Distribution</TableCell>
                                <TableCell align="center"># Shares</TableCell>
                                <TableCell align="center">Frequency</TableCell>
                                <TableCell align="center"> </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map((row, i) => {
                                return (
                                        <TableRow>
                                            <TableCell align="center">
                                                <input
                                                    value={row.stock}
                                                    name="stock"
                                                    onChange={(e) => handleInputChange(e, i)}
                                                />
                                            </TableCell>
                                            <TableCell align="center">
                                                <input
                                                    value={row.current.price}
                                                    name="price"
                                                    onChange={(e) => handleInputChange(e, i)}
                                                />
                                            </TableCell>
                                            <TableCell align="center">
                                                <input
                                                    value={row.current.yield}
                                                    name="yield"
                                                    onChange={(e) => handleInputChange(e, i)}
                                                />
                                            </TableCell>
                                            <TableCell align={"center"}>
                                                {row.current.distribution}
                                            </TableCell>
                                            <TableCell align={"center"}>
                                                {row.current.shares}
                                            </TableCell>
                                            <TableCell align="center">
                                                <select
                                                    style={{ width: "100px" }}
                                                    name="city"
                                                    value={row.current.frequency}
                                                    onChange={(e) => handleInputChange(e, i)}
                                                >
                                                    <option value=""></option>
                                                    <option value="1">Quaterly</option>
                                                    <option value="2">Monthly</option>
                                                    <option value="3">Yearly</option>
                                                </select>
                                            </TableCell>

                                            <Button className="mr10" onClick={handleConfirm}>
                                                <DeleteOutlineIcon />
                                            </Button>
                                            {showConfirm && (
                                                <div>
                                                    <Dialog
                                                        open={showConfirm}
                                                        onClose={handleNo}
                                                        aria-labelledby="alert-dialog-title"
                                                        aria-describedby="alert-dialog-description"
                                                    >
                                                        <DialogTitle id="alert-dialog-title">
                                                            {"Confirm Delete"}
                                                        </DialogTitle>
                                                        <DialogContent>
                                                            <DialogContentText id="alert-dialog-description">
                                                                Are you sure to delete
                                                            </DialogContentText>
                                                        </DialogContent>
                                                        <DialogActions>
                                                            <Button
                                                                onClick={() => handleRemoveClick(i)}
                                                                color="primary"
                                                                autoFocus
                                                            >
                                                                Yes
                                                            </Button>
                                                            <Button
                                                                onClick={handleNo}
                                                                color="primary"
                                                                autoFocus
                                                            >
                                                                No
                                                            </Button>
                                                        </DialogActions>
                                                    </Dialog>
                                                </div>
                                            )}
                                        </TableRow>
                                );
                            })}
                        </TableBody>
                    </Table>
                    <br/>
                    <br/>
                    <TextField id="outlined-basic" label="Initial Investment" variant="outlined" style={{marginLeft: "5%"}} size={"small"}
                               type="number"
                               value={initialInvestment}
                               onChange={(e) => setInitialInvestment(e.target.value)} />
                    <TextField id="outlined-basic" label="Stock Growth %" variant="outlined" style={{marginLeft: "3%"}} size={"small"}
                               type="number"
                               value={stockGrowth}
                               onChange={(e) => setStockGrowth(e.target.value)}/>
                    <TextField id="outlined-basic" label="Dividend Growth %" variant="outlined" style={{marginLeft: "3%"}} size={"small"}
                               type="number"
                               value={dividendGrowth}
                               onChange={(e) => setDividendGrowth(e.target.value)}/>
                    <FormControl variant={"outlined"} size="small" style={{marginLeft: "5%"}}>
                        <InputLabel id="demo-select-small">Years</InputLabel>
                        <Select
                            labelId="demo-select-small"
                            id="demo-select-small"
                            value={years}
                            label="Years"
                            onChange={(e) => setYears(e.target.value)}
                        >
                            <MenuItem value="">
                                <em>None</em>
                            </MenuItem>
                            <MenuItem value={5}>5</MenuItem>
                            <MenuItem value={10}>10</MenuItem>
                            <MenuItem value={15}>15</MenuItem>
                            <MenuItem value={20}>20</MenuItem>
                            <MenuItem value={25}>25</MenuItem>
                            <MenuItem value={30}>30</MenuItem>
                            <MenuItem value={35}>35</MenuItem>
                        </Select>
                    </FormControl>
                    <Button style={{marginLeft: "5%"}} variant="contained" color="primary" onClick={handleCalculate}>Calculate</Button>
                </Box>
            {/*</TableBody>*/}
            <br/>
            <br/>
            <div style={{backgroundColor: "lightgreen"}} hidden={!showMessage}>
                <Alert severity="success" style={{marginLeft: "1%"}}>The projected total value is <strong>${Number(totalValue).toFixed(2)}</strong>, with a projected dividend of <strong>${Number(totalDividend).toFixed(2)}</strong> per year.</Alert>
            </div>

            <br/>
            <br/>
            <Table
                className={classes.table}
                size="small"
                aria-label="a dense table"
                style={{maxWidth: "50%", marginLeft: "3%"}}
            >
                <TableHead style={{backgroundColor: "lightgray"}}>
                    <TableRow>
                        <TableCell align="center">Stock</TableCell>
                        <TableCell align="center">Projected Price</TableCell>
                        <TableCell align="center">Projected Value</TableCell>
                        <TableCell align="center">Projected Shares</TableCell>
                        <TableCell align="center">Projected Dividend</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row, i) => {
                        return (
                            <TableRow>
                                <TableCell align="center">{row.stock}</TableCell>
                                <TableCell align="center">{Number(row.projected.price).toFixed(2)}</TableCell>
                                <TableCell align="center">{Number(row.projected.value).toFixed(2)}</TableCell>
                                <TableCell align="center">{Number(row.projected.shares).toFixed(2)}</TableCell>
                                <TableCell align="center">{Number(row.projected.dividend).toFixed(2)}</TableCell>
                            </TableRow>
                        )
                    })}
                </TableBody>
            </Table>

        </div>
    );
}

export default DividendCalculator;